
import { defineComponent, reactive, toRefs ,onBeforeMount, inject } from 'vue'
import ohterCase from './otherCase.vue'
export default defineComponent ({
    props: {
        itemList: [], // 接收父组件传过来得数据
	    dictObj: [] // 字典表数据获取
    },
	components: {
		ohterCase
	},
	setup() {
        const $api :any = inject('$api')
		const dataMap = reactive({
			source: {
                otherCase: [
                    {
                        name: '其他案件',
                        value: ''
                    }
                ]
            }
		})
        onBeforeMount(() => {
		})
		return {
			...toRefs(dataMap)
		}
	}
})
